body {
  margin: 0;
  padding: 0;
  font-family: 'Source Sans Pro', sans-serif;
  font-weight: 300;
  font-size: 18px;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: rgb(240, 240, 240);
  color: rgb(55, 66, 70);
}

h1 {
  font-weight: 700;
  font-size: 44px;
  letter-spacing: -.021em;
  font-family: Helvetica, sans-serif;
  margin: 0;
}

h2 { 
  font-family: Helvetica, sans-serif;
  font-weight: 700;
  font-size: 34px;
  letter-spacing: -.021em;
  margin: 0;
}

h3 {
  font-family: Helvetica, sans-serif;
  font-weight: 700;
  font-size: 24px;
  margin: 0;
}

h4 {
  font-family: Helvetica, sans-serif;
  font-weight: 700;
  font-size: 19px;
  margin: 0;
}

h5 {
  font-family: Helvetica, sans-serif;
  font-weight: 700;
  font-size: 16px;
  margin: 0;
}

p {
  font-family: 'Merriweather', serif;
  font-weight: 400;
  font-size: 15px;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}
.headersplit {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    background-color: #fff;
    width: 100%;
}

.logo {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 5px;
    color: #061f4a;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: flex-start;
            justify-content: flex-start;
    -webkit-align-items: center;
            align-items: center;
    padding: 10px;
    padding-left: 37px;
}

.orange {
    color:#dd361c;
}

.WelcomeContainer {
    margin-top: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;

}

.InstructionContainer {
    margin: 10px 40px;
    padding: 30px;
    max-width: 1000px;
    background-color:white;
    text-align: left;
}

.MainWelcomeContainer {
    padding: 10px;
}

.EnterButtonHolder {
    text-align: center;
}
.searchContainer {
    margin: 0px 40px;
    margin-top: 30px;
    padding: 30px;
    background-color: white;
    font-family: 'Source Sans Pro', sans-serif;
}
.searchDiv {
    height: 60px;
    font-family: 'Source Sans Pro', sans-serif;
}
.searchContainerInside {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}
.additionalsearchparams {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
}
.search {
    position: relative;
    margin: 0px 5px;
    border: none;
    border-left: 7px solid rgb(240, 240, 240);
    border-right: 7px solid rgb(240, 240, 240);
    background: rgb(240, 240, 240);
    padding: 6px 5px;
    z-index: 2;
    font-family: 'Source Sans Pro', sans-serif;
}
label {
    font-family: 'Source Sans Pro', sans-serif;
    width: 173px;
    margin: 0 auto;
    text-align: left;
    display: block;
    position: relative;
    color: rgb(55, 66, 70);
    z-index: 1;
    top: -20px;
    transition: all 0.1s linear;
    font-size: 16px;
}
.search:focus {
    outline: none;
    border-left: 7px solid #061f4a;
}
.search:focus + label {
    top: -50px;
    font-size: 14px;
}
.search:focus::-webkit-input-placeholder { color:transparent; }
.search:focus:-moz-placeholder { color:transparent; }
.search:focus::-moz-placeholder { color:transparent; } 
.search:focus:-ms-input-placeholder { color:transparent; } 
.hidden {
    display: none;
}
.searchButton {
    font-family: 'Source Sans Pro', sans-serif;
    height: 29px;
    width: 192px;
    border: 1px solid #061f4a;
    background-color: #061f4a;
    padding: 5px;
    color: #fff;
    transition: 0.3s all ease-in-out;
}
.searchButton:hover {
    cursor: pointer;
    background-color: #dd361c;
    border: 1px solid #dd361c;
}
.searchButton:active {
    background-color: rgb(240, 240, 240);
    color: rgb(55, 66, 70);
    outline: none;
}
.searchButton:focus {
    outline: none;
}
.showHideButton {
    position: relative;
    top: -30px;
}

.showHideButton:hover {
    cursor: pointer;
}
.resultCard{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    padding: 30px;
    margin: 20px;
    background-color: white;
    border-left: 10px solid #fff;
    border-right: 10px solid #fff;
    transition: all .3s ease-in-out;
}

.resultCard:hover{
    padding-right: 20px;
    padding-left: 40px;
    border-left: 10px solid #dd361c;
    border-right: 10px solid #fff;
    cursor: pointer;
}

.resultCard:active{
    border-left: 10px solid #02bfe7;
}

.imageHalf {
    -webkit-flex: 1 1;
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 10px;
    background-color: black;
}

.detailHalf {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 10px;
}

.imageResult {
    max-width: 100%;
    max-height: 250px;
}
.resultsContainer {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(500px, 1fr));
    grid-auto-rows: 1fr;
    padding: 20px;
}
.buttonsContainer {
    margin-top: 29px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-flex-wrap: wrap;
            flex-wrap: wrap;
    -webkit-justify-content: center;
            justify-content: center;
}
.directionButton {
    margin: 0px 5px;
    font-family: 'Source Sans Pro', sans-serif;
    height: 29px;
    width: 192px;
    border: 1px solid #061f4a;
    background-color: #061f4a;
    padding: 5px;
    color: #fff;
    transition: 0.3s all ease-in-out;
}
.directionButton:hover {
    cursor: pointer;
    background-color: #dd361c;
    border: 1px solid #dd361c;
}
.directionButton:active {
    background-color: white;
    color: rgb(69, 83, 88);
    outline: none;
}
.directionButton:focus {
    outline: none;
}

.dimBackground {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,.5);
    z-index: 3;
}
.exitModal {
    position: absolute;
    top: 2px;
    right: 0px;
    padding: 5px;
    padding-top: 5px;
    border: none;
    background: white;
    color: #061f4a;
    font-size: 16px;
    border: none;
    transition: 0.3s all ease-in-out;
}
.exitModal:hover {
    color: #dd361c;
    padding-left: 0px;
    padding-right: 10px;
    outline: none;
    cursor: pointer;
}
.exitModal:active {
    outline: none;
}
.exitModal:focus {
    outline: none;
}
.modalContainer {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    width: 80%;
    height: 50%;
    top: 50%;
    left: 50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    padding: 30px;
    background-color: white;
    border-left: 10px solid #dd361c;
    border-right: 10px solid #fff;
    position: fixed;
}

.modalImageHalf {
    -webkit-flex: 1 1;
            flex: 1 1;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    margin: 10px;
}

.modalDetailHalf {
    -webkit-flex: 1 1;
            flex: 1 1;
    margin: 10px;
    overflow: scroll;
    overflow-x: hidden;
}

.modalImageResult {
    max-width: 100%;
    max-height: 100%;
    object-fit: contain;
}

.modalImageResult:hover{
    cursor: pointer;
}

.alignLeft {
    text-align: left;
}
